.card {
	position: relative;
	width: 250px;
	height: 250px;
}
.card img {
	width: 100%;
	display: block;
	border: 2px solid #fff;
	border-radius: 6px;
}

.card .front {
	transform: rotateY(90deg);
	transition: all 0.2s ease-in;
	position: absolute;
}

.flipped .front {
	transform: rotateY(0deg);
	transition-delay: 0.2s;
}

.card .back {
	transition: all 0.2s ease-in;
	transition-delay: 0.2s;
}

.flipped .back {
	transform: rotateY(90deg);
	transition-delay: 0.2s;
}
